<template>
  <div class="questionnaireNotFinish" :class="{themeL:bindThemeL}">
    <!-- 头部 -->
    <navigation-bar @onLeftClick="onLeftClick" :pageName="title">
      <template v-slot:nav-right>
        <div @click="wechartShare">
          <img
              class="nav-bar-custom-icon"
              src="../../../assets/images/mall/mall_icon_share.png"
              alt
          />
        </div>
      </template>
    </navigation-bar>

    <!-- 问卷详情 -->
    <section class="list">
      <form id="question">
        <div class="item" v-if="isDisplayed == 0">
          <div class="questionnaireTitle">
            <div class="leftImg">
              <img :src="iconImg" class="iconImg">
            </div>
            <div class="rightCon">{{titleName}}</div>
          </div>
          <div class="questionHeader">Q{{titleNo}} : {{questName}} ({{ questionType}})</div>

          <!-- 问答题 -->
          <div class="questionCon questionInput" v-if="list.QUEST_TYPE_FLAG == 0">
            <van-cell-group>
              <van-field
                  v-model="message"
                  rows="1"
                  autosize
                  maxlength="100"
                  type="textarea"
                  placeholder="请输入"
              />
              <div class="txtLimit">{{message.length}}/100</div>
            </van-cell-group>
          </div>

          <!-- 单选 -->
          <div v-if="list.QUEST_TYPE_FLAG == 1 && list.SELECT_TYPE_FLAG == 1">
            <van-radio-group v-model="radio">
              <van-radio checked-color="#FB5768" v-for="(item,index) in list.optionList" :name="item.itemCode"
                         :key="index">{{item.itemName}}
              </van-radio>
            </van-radio-group>
          </div>

          <!-- 多选 -->
          <div v-if="list.QUEST_TYPE_FLAG == 1 && list.SELECT_TYPE_FLAG == 0">
            <div>
              <van-checkbox-group v-model="result">
                <van-checkbox checked-color="#FB5768" v-for="(item,index) in list.optionList" :key='index'
                              :name="item.itemCode">{{item.itemName}}
                </van-checkbox>
              </van-checkbox-group>
            </div>

          </div>


        </div>
        <div class="item" v-if="isDisplayed == 1">
          <div class="questionnaireTitle">
            <div class="leftImg">
              <img :src="iconImg" class="iconImg">
            </div>
            <div class="rightCon">{{titleName}}</div>
          </div>
          <div class="questionHeader">Q{{titleNo}} : {{questName}} ({{ questionType}})</div>

          <!-- 问答题 -->
          <div class="questionCon questionInput" v-if="list.QUEST_TYPE_FLAG == 0">
            <van-cell-group>
              <van-field
                  v-model="message"
                  rows="1"
                  autosize
                  disabled
                  type="textarea"
                  placeholder="请输入"
              />
            </van-cell-group>
          </div>

          <!-- 单选 -->
          <div v-if="list.QUEST_TYPE_FLAG == 1 && list.SELECT_TYPE_FLAG == 1">
            <van-radio-group v-model="radio">
              <van-radio checked-color="#FB5768" v-for="(item,index) in list.optionList" :name="item.itemCode"
                         :key="index">{{item.itemName}}
              </van-radio>
            </van-radio-group>
          </div>

          <!-- 多选 -->
          <div v-if="list.QUEST_TYPE_FLAG == 1 && list.SELECT_TYPE_FLAG == 0">
            <div>
              <van-checkbox-group v-model="result">
                <van-checkbox checked-color="#FB5768" v-for="(item,index) in list.optionList" :key='index'
                              :name="item.itemCode">{{item.itemName}}
                </van-checkbox>
              </van-checkbox-group>
            </div>

          </div>


        </div>
      </form>
    </section>

    <section class="pageIndicator">
      {{titleNo}}/{{totalNo}}
    </section>

    <section class="bottomBtn" v-if="btnFlag == 0">
      <submit-btn :submitName="btnName" @save='next'></submit-btn>
    </section>

    <section class="bottomBtn" v-if="btnFlag == 1">
      <submit-btn @save='submit'></submit-btn>
    </section>

    <van-action-sheet v-model="show.weChartShare" cancel-text="取消" @cancel="onCancel" round safe-area-inset-bottom>
      <p class="shareTit">分享至</p>
      <div class="share-box" :style="{'display':'flex','justify-content':'center'}">
        <div class="shareChart" @click="shareTo(1)">
          <img :src="icon.shareChart"/>
          <p>微信好友</p>
        </div>
        <div class="shareFriends">
          <img :src="icon.shareFriends" @click="shareTo(2)"/>
          <p>朋友圈</p>
        </div>
      </div>
    </van-action-sheet>
  </div>
</template>

<script>
  import NavigationBar from '@/common/currency/NavigationBar.vue';
  // import QuestionnaireItem from '@/common/servers/questionnaire/QuestionnaireItem.vue';
  import SubmitBtn from '@/common/currency/SubmitBtn';
  import {getQuestInfo, savePaperReaderNew, savePaperAnswer, getPaperShareUrl} from '@/services/servers/question';
  import {Toast} from 'vant';
  import {gShareAction} from "../../../utils/common";

  export default {
    name: 'questionnaireNotFinish',
    components: {
      NavigationBar,
      SubmitBtn
    },
    data() {
      return {
        bindThemeL: this.$curTheme == 0 ? false : true,
        title: '问卷详情',
        btnName: '下一题',
        btnFlag: 0,
        isDisplayed: 0,
        isNowForbid: true,
        questionType: "单选",
        message: '',
        radio: '',
        nowNum: 1,
        isShow: true,
        list: {},
        result: [],
        item: {},
        con: {},
        questData: [],
        titleName: '',
        titleNo: '',
        totalNo: '', //题目总数
        questName: '',
        isSaveing: false,
        iconImg: require('@/assets/images/home/question/question.png'),
        show: {
          weChartShare: false //微信分享
        },
        icon: {
          shareChart: require("@/assets/images/common/nav_icon_share_wechat.png"),
          shareFriends: require("@/assets/images/common/nav_icon_share_moments.png")
        },
        nextLinkFlag: 0, //下一题是否有链接跳转
        nextQuestCode: "" //下一题跳转题目的code
      }
    },
    mounted() {
      this.item = this.$route.query
      this.bindThemeL = this.$curTheme == 0 ? false : true;
      console.log("【问卷】 mounted： item = " + JSON.stringify(this.item))
      this.initData();
      this.isRead();
    },
    methods: {
      onLeftClick() {
        this.$store.dispatch("changeActiveFlag", 0);
        this.$router.go(-1);
      },
      //获取第一题信息
      initData() {
        this.getQuestInfo(this.item.paperCode, "", this.nowNum)
      },

      //保存问卷已读
      isRead() {
        let param = {
          "publishCode": this.item.publishCode,
          "paperCode": this.item.paperCode,
          "userCode": this.$userInfo.OWNER_CODE,
          "roomCode": this.$userInfo.ROOM_CODE
        };
        console.log("【问卷】 isRead: param = " + JSON.stringify(param))

        savePaperReaderNew(param).then(
          res => {
            if (res.resultCode === "0") {
              //提交成功
              console.log("【问卷】isRead：success.");
            } else {
              console.log("【问卷】isRead：fail msg = " + res.msg);
            }
          }
        ).catch(error => {
          console.log("【问卷】isRead：error = " + error);
        })
      },

      //获取下一题信息
      next() {
        //保存回答到本地
        if (this.saveAnswerLocal()) {
          //获取下一题信息
          if (this.nextLinkFlag == 0) {
            this.getQuestInfo(this.item.paperCode, "", this.nowNum, true);
          } else {
            this.getQuestInfo(this.item.paperCode, this.nextQuestCode, -1, true);
          }
        }
      },

      //本地保存答案
      saveAnswerLocal() {
        this.nextLinkFlag = 0;
        this.nextQuestCode = "";

        let content = {};
        if (this.questionType == "单选") {
          if (this.radio <= 0) {
            Toast('请做出您的选择...')
            return false;
          }
          let questItem = {};
          this.list.optionList.forEach((item) => {
            if (item.itemCode == this.radio) {
              this.nextLinkFlag = item.linkQuestFlag;
              this.nextQuestCode = item.linkQuestCode;
            }
          })
          questItem.questCode = this.con.questCode;
          questItem.itemCode = this.radio || '';
          questItem.content = '';
          questItem.point = 0;
          this.questData.push(questItem);

        } else if (this.questionType == "多选") {
          if (this.result <= 0) {
            Toast('请做出您的选择...')
            return false;
          }
          if (this.result.length > this.con.mutiCnt) {
            Toast('最多选择' + this.con.mutiCnt + '个选项')
            return false;
          }
          this.list.optionList.forEach((item) => {
            if (item.itemCode == this.result[0]) {
              this.nextLinkFlag = item.linkQuestFlag;
              this.nextQuestCode = item.linkQuestCode;
            }
          })
          for (let i = 0; i < this.result.length; i++) {
            let questItem = {};
            questItem.questCode = this.con.questCode;
            questItem.itemCode = this.result[i];
            questItem.content = '';
            questItem.point = 0;
            this.questData.push(questItem);
          }
        } else {
          if (this.message.length <= 0) {
            Toast('请输入您的意见...')
            return false;
          }
          /* 问答题跳转功能 */
          if (this.list.optionList.length > 0) {
            this.nextLinkFlag = this.con.optionList[0].linkQuestFlag;
            this.nextQuestCode = this.con.optionList[0].linkQuestCode;
          }
          let questItem = {};
          questItem.questCode = this.con.questCode;
          questItem.content = this.message;
          this.questData.push(questItem);
        }

        console.log("【问卷】saveAnswerLocal: 答案=" + JSON.stringify(this.questData));
        return true;
      },

      //获取问题信息
      getQuestInfo(paperCode, questCode, sortNo, forceUpdate = false) {
        console.log("【问卷】 getQuestInfo： paperCode = " + paperCode + ",questCode=" + questCode + ",sortNo=" + sortNo)
        let param = {
          paperCode: paperCode
        };
        if (questCode != null && questCode != "") {
          param.questCode = questCode;
        }
        if (sortNo > 0) {
          param.sortNo = sortNo;
        }
        this.isShow = false;
        getQuestInfo(param).then(
          res => {
            if (res.resultCode === "0") {
              if (res.data.lastFlag) {
                //最后一题，显示提交按钮
                this.btnFlag = 1;
                //this.isDisplayed = 1;
              }
              this.radio = '';
              this.result = [];
              this.message = '';

              this.con = res.data;
              this.isShow = true;
              this.nowNum = this.con.sortNo + 1; //更新下一题的序号
              this.titleName = this.con.paperName;
              this.titleNo = this.con.sortNo;
              this.totalNo = this.con.maxSortNo;
              this.questName = this.con.questName;
              this.list.optionList = this.con.questItemList;
              this.list.QUEST_TYPE_FLAG = this.con.questTypeFlag;
              this.list.SELECT_TYPE_FLAG = this.con.selectTypeFlag;
              console.log("【问卷】 getQuestInfo: 问题列表=" + JSON.stringify(this.list.optionList))
              if (forceUpdate) {
                this.$forceUpdate();
              }
              this.isTypeQus()
            } else {
              console.log("getQuestInfo: error = " + res.msg)
            }
          }
        )
      },

      //提交答案
      savePaperAnswer() {
        console.log("【问卷】 savePaperAnswer: " + JSON.stringify(this.questData))
        let param = {
          "paperCode": this.item.paperCode,
          "publishCode": this.item.publishCode,
          "questAnswerList": this.questData,
          "roomCode": this.$userInfo.ROOM_CODE,
          "userCode": this.$userInfo.OWNER_CODE
        };
        console.log("【问卷】 savePaperAnswer: param = " + JSON.stringify(param))
        this.isSaveing = true;
        savePaperAnswer(param).then(
          res => {
            /*res = {
              resultCode: "0",
              msg: "提交成功",
              data:{
                //超链接
                forwardType: 0,
                forwardUrl: "https://www.baidu.com"
                //大转盘
                //forwardType: 1,
                //forwardUrl: "11"
              }
            }*/
            if (res.resultCode === "0") {
              console.log("【问卷】 savePaperAnswer: response = " + JSON.stringify(res))
              //提交成功
              this.isSaveing = false;
              this.$store.dispatch("changeActiveFlag", 0);
              this.$toast(res.msg)

              //根据后台配置进行问卷跳转
              if (res.data != null && JSON.stringify(res.data) != '{}') {
                if (res.data.forwardType == 0) {
                  //退出问卷答题页面
                  this.$router.go(-1);
                  //超链接跳转
                  if (res.data.forwardUrl != null && res.data.forwardUrl.length > 0) {
                    this.openWebUrl(res.data.forwardUrl)
                  }
                } else if (res.data.forwardType == 1) {
                  //跳转到大转盘
                  if (res.data.forwardUrl != null && res.data.forwardUrl.length > 0) {
                    this.$router.replace({
                      name: 'pointActivity',
                      query: {
                        promotionId: res.data.forwardUrl,
                      }
                    })
                  }
                } else {
                  //不支持的跳转类型
                  this.$router.go(-1);
                }
              } else {
                //无需跳转，返回上一级页面
                this.$router.go(-1);
              }
            } else {
              this.isSaveing = false;
              this.$toast(res.msg)
            }
          }
        ).catch(error => {
          console.log("【Exception】savePaperAnswer：" + error);
          this.isSaveing = false;
        })
      },

      //跳转第三方链接
      openWebUrl(url) {
        if (this.$devicePlatform == "pc") {
          window.open(url)
          return;
        }
        const platform = navigator.userAgent;
        const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
        if (!isiOS) {
          cordova.InAppBrowser.open(
            url,
            "_blank",
            "location=yes,toolbar=yes,toolbarcolor=#ffffff,hideurlbar=yes,title=yes,titletext=问卷跳转,titlecolor=#000000"
          );
        } else {
          cordova.InAppBrowser.open(
            url,
            "_blank",
            "location=no,toolbar=yes,toolbarposition=top,toolbarcolor=#ffffff,title=yes,titletext=问卷跳转,titlecolor=#000000"
          );
        }
      },

      // 点击提交按钮
      submit() {
        if (this.isSaveing) {
          return;
        }
        //保存最后一题回答
        if (this.saveAnswerLocal()) {
          //提交答案
          this.savePaperAnswer();
        }
      },

      // 题型判断
      isTypeQus() {
        if (this.list.QUEST_TYPE_FLAG == 1 && this.list.SELECT_TYPE_FLAG == 1) {
          this.questionType = "单选"
        } else if (this.list.QUEST_TYPE_FLAG == 1 && this.list.SELECT_TYPE_FLAG == 0) {
          this.questionType = "多选"
        } else if (this.list.QUEST_TYPE_FLAG == 0) {
          this.questionType = "问答"
        }
      },

      // 点击微信分享
      wechartShare() {
        // 点击分享弹窗
        this.show.weChartShare = true;
      },

      //取消微信分享
      onCancel() {
        this.show.weChartShare = false;
      },

      // 微信分享, 1: 好友  2：朋友圈
      shareTo(type) {
        let _this = this;

        //获取分享url
        let param = {
          "ownerCode": this.$userInfo.OWNER_CODE,
          "areaCode": this.$userInfo.AREA_CODE,
          "shareTo": "1", //1：微信
          "paperCode": this.item.paperCode
        };
        console.log("shareTo:: param = "+JSON.stringify(param))
        getPaperShareUrl(param).then(response => {
          console.log("shareTo: response = "+JSON.stringify(response))
          if (response.resultCode == "0") {
            _this.show.weChartShare = false;
            let shareUrl = response.data;
            console.log("shareTo: shareUrl = "+shareUrl+",type="+type)
            gShareAction.share(type, {
              shareUrl: shareUrl,
              title: "问卷分享",
              description: "更多精彩，尽在信我家",
              thumb: ""
            },function(){
              _this.$toast("分享成功")
            },function(){
              _this.$toast("分享失败")
            })

          } else {
            console.log("shareTo: get share url fail = "+error);
          }
        }).catch(error => {
          console.log("shareTo: get share url error = "+error);
        });
      }
    }
  }
</script>

<style scoped lang="scss">
  .themeL.questionnaireNotFinish {
    .leftImg {
      width: 28px;
      height: 28px;
    }

    .rightCon {
      line-height: 28px;
      font-size: 18px;
    }

    .questionHeader {
      font-size: 15px;
    }

    /deep/ .van-checkbox__label {
      font-size: 15px;
    }

    .van-field__control {
      font-size: 15px;
    }

    .txtLimit {
      font-size: 15px;
    }
  }

  .questionnaireNotFinish {
    width: 100%;
    height: 100%;
    display: flex;
    overflow: hidden;
    flex-direction: column;
    background-color: #f9f9f9;
  }

  /*微信分享图标*/
  .nav-bar-custom-icon {
    width: 30px;
    height: 30px;
  }

  .questionnaireNotFinish .list {
    margin: 9px 0 0 0;
    padding-bottom: 12px;
    background-color: #ffffff;
    margin: 15px 15px 0 15px;
    box-shadow: 0 0 6px 0 rgba(219, 219, 219, 0.50);
    border-radius: 5px;
    overflow-y: auto;
  }

  .questionnaireNotFinish .questionnaireTitle {
    width: calc(100% - 24px);
    /* height: 24px; */
    padding: 12px;
    display: flex;
    border-bottom: 1px solid #f8f8f8
  }

  .questionnaireNotFinish .leftImg {
    height: 24px;
    width: 24px;
    margin-right: 12px
  }

  .questionnaireNotFinish .leftImg img {
    display: block;
    width: 100%;
    height: 100%;
  }

  .questionnaireNotFinish .rightCon {
    flex: 1;
    /* height: 24px; */
    line-height: 24px;
    font-size: 14px;
    word-break: break-word;
  }

  .questionnaireNotFinish .bottomBtn {
    width: 100%;
    height: 70px;
    line-height: 70px;
    /* overflow: hidden; */
    /*margin-top: 7px;*/
  }

  .questionnaireNotFinish .questionHeader {
    /* height: 20px; */
    font-size: 14px;
    width: calc(100% - 24px);
    padding: 12px;
  }

  .questionnaireNotFinish .questionCon {
    height: auto;
  }

  .questionnaireNotFinish .questionInput {
    min-height: 120px;
  }

  .questionnaireNotFinish .van-hairline--top-bottom::after {
    border: none;
    border-width: 0;
  }

  .questionnaireNotFinish .van-cell-group {
    border: none;
    border-width: 0;
  }

  .questionnaireNotFinish .van-cell {
    padding-bottom: 0;
  }

  .questionnaireNotFinish .van-cell:not(:last-child)::after {
    border-bottom: none;
  }

  .questionnaireNotFinish .txtLimit {
    height: 10px;
    line-height: 10px;
    font-size: 10px;
    border: none;
    text-align: right;
    margin: 0 16px;
    padding: 10px 5px;
    background: #f8f8f8;
    color: #9A9A9A;
  }

  /*页码*/
  .pageIndicator {
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #333333;
    letter-spacing: 0;
    text-align: center;
    margin-top: 14px;
  }

  /*微信分享*/
  .shareTit {
    font-family: PingFangSC-Medium;
    font-size: 16px;
    color: #333333;
    letter-spacing: -0.05px;
    text-align: center;
    height: 22px;
    line-height: 22px;
    padding: 25px 0;
    margin: 0;
  }

  .share-box {
    height: 104px;
    margin: 0 15px;
    box-sizing: border-box;
    border-bottom: 0.5px solid #c8c8c8;

    img {
      width: 64px;
      height: 64px;
    }

    p {
      height: 21px;
      line-height: 21px;
      margin: 0;
      margin-top: 4px;
    }
  }

  .shareChart {
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #9a9a9a;
    letter-spacing: -0.04px;
    text-align: center;
